                     background-repeat: no-repeat;\r\n                            background-size: 60%;\r\n                        }\r\n                    }\r\n\r\n                    & > .container {\r\n                    }\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n","@import \"./variables\";\r\n@import \"./breadcrum\";\r\n@import \"./sidebar\";\r\n@import \"./landing-page\";\r\n@import \"./smart-light.scss\";\r\n\r\n// override defaults\r\n.main-block {\r\n    &.render-ui {\r\n        & > .content-block {\r\n            // * {\r\n            //     // color: #424242;\r\n            // }\r\n            // // Hide default header\r\n            // & > .header-block {\r\n            //     // display: none !important;\r\n            // }\r\n\r\n            & > .main-content-block {\r\n                padding: 0px !important;\r\n                // height: 100% !important;\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n.lxp-user-profile-container {\r\n    &.mda-spa-user-profile {\r\n        padding: 0 15px;\r\n        cursor: pointer;\r\n\r\n        & > .profile-button {\r\n            & > .profile-image-container {\r\n                background-color: $profile-icon-background-color  !important;\r\n                & > .name {\r\n                    color: inherit !important;\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n.profile-details-backdrop{\r\n    z-index: 1001;\r\n}\r\n.profile-details-panel {\r\n    &.mda-spa-user-profile {\r\n        padding: 0;\r\n        & > .header {\r\n            .profile-image {\r\n                & > .profile-image-container {\r\n                    background-color: $profile-icon-background-color !important;\r\n                }\r\n            }\r\n        }\r\n        & > .toolbar {\r\n            display: flex;\r\n            align-items: center;\r\n            flex-direction: column;\r\n            border-top: 1px solid silver;\r\n            border-bottom: 1px solid silver;\r\n\r\n            &.no-border {\r\n                border: none;\r\n            }\r\n\r\n            &.logout {\r\n                padding: 20px 0;\r\n            }\r\n\r\n            .row {\r\n                display: flex;\r\n                align-items: center;\r\n                justify-content: space-between;\r\n                padding: 10px;\r\n\r\n                & > .label {\r\n                    margin-right: 30px;\r\n                }\r\n            }\r\n\r\n            .uxp-button {\r\n                margin-right: 0px;\r\n\r\n                &:last-child {\r\n                    background-color: #f0f0f0;\r\n                }\r\n\r\n                &:hover {\r\n                    background-color: white;\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n.mda-spa-web-ui-container {\r\n    width: 100%;\r\n    height: 100%;\r\n    display: flex;\r\n    overflow-x: hidden;\r\n\r\n    @include sidebarStyles();\r\n\r\n    & > .main {\r\n        flex: 1 1 0%;\r\n        position: relative;\r\n        padding: 0px;\r\n        padding-top: 60px;\r\n\r\n        & > .header {\r\n            position: absolute;\r\n            width: 100%;\r\n            height: 60px;\r\n            top: 0;\r\n            left: 0;\r\n            display: flex;\r\n            align-items: center;\r\n            justify-content: space-between;\r\n            background: white;\r\n            padding: 8px 13px;\r\n            box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);\r\n\r\n            & > .middle {\r\n                .title {\r\n                    font-size: 14px;\r\n                    flex-grow: 1;\r\n                    text-align: center;\r\n                    font-weight: 500;\r\n                }\r\n            }\r\n            & > .right {\r\n                display: flex;\r\n                align-items: center;\r\n                justify-content: flex-end;\r\n            }\r\n        }\r\n\r\n        & > .page-content {\r\n            width: 100%;\r\n            height: 100%;\r\n            overflow-y: auto;\r\n            padding: 2px;\r\n        }\r\n    }\r\n}\r\n\r\n.line-clamp {\r\n    display: block;\r\n    display: -webkit-box;\r\n    -webkit-box-orient: vertical;\r\n    position: relative;\r\n    line-height: 1.25;\r\n    overflow: hidden;\r\n    text-overflow: ellipsis;\r\n    padding: 0 !important;\r\n}\r\n\r\n.line-clamp-3 {\r\n    -webkit-line-clamp: 3;\r\n    min-height: 45px !important;\r\n}\r\n\r\n.loading-text {\r\n    margin-top: 50px;\r\n    margin-bottom: 20px;\r\n    text-align: center;\r\n}\r\n\r\n.collapsible-section {\r\n    margin-top: 20px;\r\n    background: #ffffff;\r\n    box-shadow: 0 1px 3px 0 rgb(0 0 0 / 10%), 0 1px 2px -1px rgb(0 0 0 / 10%);\r\n    border-radius: 8.5px;\r\n\r\n    & > .header-panel {\r\n        display: inline-flex;\r\n        align-items: center;\r\n        width: 100%;\r\n        padding: 10px;\r\n        padding-left: 30px;\r\n        justify-content: space-between;\r\n\r\n        button.toggle {\r\n            align-self: center;\r\n            margin-left: 10px;\r\n            transition: all 0.5s ease;\r\n        }\r\n\r\n        button.toggle.is-expanded {\r\n            transform: rotateZ(180deg);\r\n        }\r\n\r\n        .title {\r\n            cursor: pointer;\r\n            font-size: 15px;\r\n            font-weight: 700;\r\n        }\r\n\r\n        & > .leftbar {\r\n            display: flex;\r\n        }\r\n    }\r\n\r\n    .content {\r\n        overflow: hidden;\r\n        max-height: 0px;\r\n        transition: max-height 0.6s ease-in-out;\r\n        overflow: scroll;\r\n    }\r\n\r\n    .content[aria-expanded=\"true\"] {\r\n        max-height: 850px;\r\n        transition: max-height 0.6s cubic-bezier(0, 1, 0, 1);\r\n    }\r\n}\r\n\r\n.mda-spa-error-page-container {\r\n    width: 100%;\r\n    height: 100%;\r\n    background-color: transparent;\r\n\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n\r\n    .overlay {\r\n        width: auto;\r\n        height: auto;\r\n        padding: 20px;\r\n        // box-shadow: 0 0 20px 6px #efb1b178;\r\n        border-radius: 20px;\r\n        display: flex;\r\n        flex-direction: column;\r\n        align-items: center;\r\n        justify-content: center;\r\n        transform: translateY(-20%);\r\n\r\n        .icon-container {\r\n            width: 100px;\r\n            height: 100px;\r\n            background-color: transparent;\r\n\r\n            .icon {\r\n                width: 100%;\r\n                height: 100%;\r\n                background-image: url($unhappyFace);\r\n                background-position: center;\r\n                background-repeat: no-repeat;\r\n                background-size: 50%;\r\n                filter: invert(24%) sepia(55%) saturate(6108%) hue-rotate(328deg) brightness(105%) contrast(80%);\r\n            }\r\n        }\r\n\r\n        .message {\r\n            width: auto;\r\n            height: auto;\r\n            display: flex;\r\n            align-items: center;\r\n            flex-direction: column;\r\n            color: #e5316f;\r\n\r\n            .title {\r\n                padding: 20px;\r\n                font-size: 20px;\r\n                font-weight: 900;\r\n            }\r\n\r\n            .sub-title {\r\n                font-size: 14px;\r\n            }\r\n\r\n            & > .uxp-button {\r\n                margin: 15px 0;\r\n                background-color: #383838;\r\n                color: #767676;\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n\r\n.uxp-input-container {\r\n    &.readonly {\r\n        cursor: not-allowed;\r\n        padding: 15px 20px;\r\n    }\r\n}","// let's create some variables here and use them in the styles\r\n$sidebar-width: 250px;\r\n$sidebar-item-text-color-active: #424242;\r\n$sidebar-item-text-color: white;\r\n$sidebar-item-background-color: transparent;\r\n$sidebar-item-background-color-active: #4cd8f9;\r\n$sidebar-background-color: #171B7A;\r\n\r\n$profile-icon-background-color: #171B7A;\r\n\r\n// #424242\r\n@mixin GrayFilterForSVG {\r\n    filter: saturate(100%) brightness(0%) invert(24%) sepia(0%) saturate(689%) hue-rotate(266deg) brightness(94%)\r\n        contrast(87%);\r\n}\r\n\r\n@mixin WhiteFilterForSVG {\r\n    filter: saturate(100%) brightness(0%) invert(100%);\r\n}\r\n\r\n\r\n$unhappyFace: \"data:image/svg+xml;base64,\";\r\n\r\n",".smart_light {\r\n\r\n    padding: 15px 10px;\r\n    border-radius: 0;\r\n\r\n    .smart-light-box {\r\n        justify-content: space-between;\r\n        align-items: center;\r\n        padding: 0px 0px;\r\n        min-height: 60px;\r\n        flex-grow: 0;\r\n        background-color: #ffffff;\r\n        // background-color: #011217;\r\n        color: #00000078;\r\n        width: 25%;\r\n        border-radius: 10px;\r\n        overflow: hidden;\r\n        position: absolute;\r\n\r\n        bottom: 2em;\r\n        left: 2em;\r\n        z-index: 1;\r\n        outline: none;\r\n\r\n\r\n        &:before {\r\n            content: \"\";\r\n            position: absolute;\r\n            top: 0;\r\n            right: 0;\r\n            bottom: 0;\r\n            left: 0;\r\n            z-index: -1;\r\n            margin: 0px;\r\n            border: 2px solid #40404063;\r\n            background: linear-gradient(180deg,#e11d5e) border-box;\r\n            -webkit-mask:\r\n                linear-gradient(#fff 0 0) padding-box,\r\n                linear-gradient(#fff 0 0);\r\n            -webkit-mask-composite: destination-out;\r\n            mask:\r\n                linear-gradient(#fff 0 0) padding-box,\r\n                linear-gradient(#fff 0 0);\r\n            // background: linear-gradient(180deg, #8A1538, #20EBCC, #B71742, #C00A3A) border-box;\r\n            // -webkit-mask:\r\n            //     linear-gradient(#fff 0 0) padding-box,\r\n            //     linear-gradient(#fff 0 0);\r\n            // -webkit-mask-composite: destination-out;\r\n            // mask:\r\n            //     linear-gradient(#fff 0 0) padding-box,\r\n            //     linear-gradient(#fff 0 0);\r\n            mask-composite: destination-out;\r\n            mask-composite: exclude;\r\n            border-radius: 10px;\r\n            outline: none;\r\n\r\n        }\r\n\r\n        .smart-light-box-top {\r\n            display: inline-flex;\r\n            width: 100%;\r\n            padding: 15px 15px;\r\n            border-bottom: 1px solid #40404063;\r\n\r\n            .smart-light-box_lft {\r\n                display: inline-block;\r\n                width: 50%;\r\n\r\n                h4 {\r\n                    display: inline-block;\r\n                    font-size: 1.1em;\r\n                    color: rgb(0, 0, 0);\r\n                }\r\n            }\r\n\r\n            .smart-light-box_rht {\r\n                display: inline-block;\r\n                width: 50%;\r\n\r\n                .showcase-input {\r\n                    width: 100% !important;\r\n                    padding: 0;\r\n\r\n                    .uxp-form-select {\r\n                        min-width: 100%;\r\n\r\n                        .uxp-select-placeholder.dynamic-select {\r\n                            background: #dcdcdc;\r\n                        }\r\n\r\n                        .uxp-select-placeholder.valid:before {\r\n                            filter: none;\r\n                        }\r\n                    }\r\n                }\r\n\r\n\r\n\r\n            }\r\n        }\r\n\r\n        .smart-light-box_details {\r\n            display: inline-block;\r\n            width: 100%;\r\n\r\n            .data-grid {\r\n                background-color: transparent;\r\n                border: 1px solid #e4e4e463;\r\n\r\n                .data-grid-column {\r\n                    border: 0px solid #77777763;\r\n                    height: 50%;\r\n\r\n                    &:nth-child(odd) {\r\n                        border-right: 1px solid #77777763;\r\n                    }\r\n\r\n                    &:first-child {\r\n                        border-bottom: 1px solid #77777763;\r\n                    }\r\n\r\n                    &:nth-child(2) {\r\n                        border-bottom: 1px solid #77777763;\r\n                    }\r\n                }\r\n\r\n                .item-card {\r\n                    background-color: transparent;\r\n                    display: inline-block;\r\n\r\n                    .item-card-image-container {\r\n                        position: inherit;\r\n                        transform: none;\r\n                        left: 0;\r\n                        top: 0;\r\n\r\n                        .profile-image-container {\r\n                            background-color: transparent !important;\r\n                            width: 22px;\r\n                            height: 22px;\r\n                            opacity: 0.8;\r\n                        }\r\n                    }\r\n\r\n                    .content {\r\n                        margin-left: 0;\r\n                        padding: 5px 0;\r\n                        text-align: center;\r\n\r\n                        .title {\r\n\r\n                            h3 {\r\n                                margin: 0;\r\n                                padding: 0;\r\n                                font-weight: 500;\r\n                                font-size: 1.65em;\r\n\r\n                                &.green {\r\n                                    // color: #4fc8ac82;\r\n                                    color: #4FC8AC;\r\n\r\n\r\n                                }\r\n\r\n                                &.pink {\r\n                                    color: #FF5D88;\r\n                                }\r\n\r\n                                &.orange {\r\n                                    color: #FF9A36;\r\n                                }\r\n\r\n                                span.white {\r\n                             